@charset "UTF-8";

:root {
    --fullname-color: #7d7d7d;
    --wording-colors: #231f20;
    --divisor-color: #39d6e4;
    --panabel-letter-color: #57575a;
    --panabel-vela-izquierda: #8cd0c9;
    --panabel-vela-derecha: #4fc1bc;
    --default-font: "Montserrat", sans-serif;
}

body {
    font-family: var(--default-font);
    color: var(--panabel-letter-color);
    font-weight: 500;
    line-height: 1.2rem;
}

.signature {
    max-width: 18rem;
    margin: 2.5rem auto;
    padding: 1rem;
    font-size: .8rem;
    /* border: 1px solid var(--panabel-letter-color);
    border-radius: 5px;
    font-family: Montserrat, 'sans-serif'; */
    box-shadow: 10px 10px 24px 1px rgba(214,214,214,0.91);
}

.logo img {
    display: block;
    max-width: 230px;
    margin: 1.2rem auto 0;
}

#jobdescription {
    text-transform: uppercase;
    margin-bottom: 0.5rem;
    /* text-align: right; */
}

/* .address {
    text-align: right;
} */

#fullname {
    text-transform: uppercase;
    color: var(--fullname-color);
    font-weight: 600;
    font-size: 1.3rem;
    /* text-align: right; */
    margin-bottom: 0.8rem;
}

.countries {
    font-size: 0.6rem;
    font-weight: bold;
    text-align: center;
    color: var(--panabel-letter-color);
    margin-bottom: 0.6rem;
}

/* Form Style */

label {
    display: block;
    padding-bottom: 0.8rem;
}

form {
    max-width: 20rem;
    margin: 3rem auto;
}

fieldset {
    border-radius: 5px;
}

legend {
    margin-bottom: 0.8rem;
    padding: 0 0.5rem;
}

input {
    margin: 0.4rem 0;
    padding: 0.3rem;
    border-radius: 8px;
}

#generatedSignature {
    margin: 2rem auto;
}

/* support Classes */
.mt {
    margin-top: .2rem;
}